/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import { ReactNode } from 'react';
import styled from '@emotion/styled';
import { useColor } from 'hooks/useColor';

interface Props {
  children: ReactNode;
}
const Conversation = ({ children }: Props) => {
  const colors = useColor();
  return <Wrapper color={colors.text.secondary}>{children}</Wrapper>;
};

const Wrapper = styled.div`
  margin: 5rem 0;
  &:before,
  &:after {
    display: block;
    content: '...';
    text-align: center;
    font-size: 30px;
    color: ${({ color }) => color};
    letter-spacing: 4px;
  }
  &:before {
    margin-bottom: 20px;
  }
  &:after {
    margin-top: 20px;
  }
`;

interface BalloonProps {
  person: string;
  contents: string;
}
const Balloon = ({ person, contents }: BalloonProps) => {
  const colors = useColor();

  return (
    <dl>
      <dt>{person}</dt>
      <dd
        css={css`
          border: 1px dashed ${colors.text.secondary};
          padding: 12px;
          border-radius: 6px;
        `}
      >
        {contents}
      </dd>
    </dl>
  );
};

Conversation.Balloon = Balloon;

export default Conversation;
